	module.exports = {"data":{"wordpressPost":{"title":"Social media, autism and diet patterns: Most read articles in February","content":"<p>The most-read article in February was <i>Brurberg </i><i>et al.&#8217;s </i>systematic review of case definitions for chronic fatigue syndrome. <em>Hsiao et al.’s</em> paper on road traffic injury mortality has also been popular. Other newly-published papers in the top ten most read include <em>Jenkins et al.&#8217;s</em> randomised controlled trial on the effect of a vegan low-carbohydrate diet on cardiovascular risk factors and body weight in hyperlipidaemic adults, and <em>Williams et al.’s</em> article on social media interventions for diet and exercise behaviours.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Brurberg <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/2/e003973.full\" target=\"_blank\">Case definitions for chronic fatigue syndrome/myalgic encephalomyelitis (CFS/ME): a systematic review</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Rao <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/12/e004277.full\" target=\"_blank\">Do healthier foods and diet patterns cost more than less healthy options? A systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Jenkins <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/2/e003505.full\">Effect of a 6-month vegan low-carbohydrate (‘Eco-Atkins’) diet on cardiovascular risk factors and body weight in hyperlipidaemic adults: a randomised controlled trial</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Krusche <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/11/e003498.full\">Mindfulness online: an evaluation of the feasibility of a web-based mindfulness course for stress, anxiety and depression</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Hsiao <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/8/e002621.full\">Road traffic injury mortality and its mechanisms in India: nationally representative mortality survey of 1.1 million homes</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Brueton <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/2/e003821.full\">Strategies to improve retention in randomised trials: a Cochrane systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Williams <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/2/e003926.full\">Social media interventions for diet and exercise behaviours: a systematic review and meta-analysis of randomised controlled trials</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Carpenter <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/5/e002299.full\" target=\"_blank\">Bed sharing when parents do not smoke: is there a risk of SIDS? An individual level analysis of five major case–control studies<br />\n</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Baird <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/2/e004278.full\" target=\"_blank\">Guidelines: is bigger better? A review of SIGN guidelines</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Taylor <i>et al.</i></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/10/e003219.full\">Prevalence and incidence rates of autism in the UK: time trend from 2004–2010 in children aged 8 years</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"da4af049-288d-552b-b8d5-6bc2c128c3b1"}}

